import Box from "@mui/material/Box";
import { DataGrid } from "@mui/x-data-grid";
import * as React from "react";
import { useNavigate } from "react-router-dom";
import { sessionContext } from "../features/Session";

var rows = [];
const columns = [
  { field: "id", headerName: "ID", width: 90 },
  {
    field: "name",
    headerName: "Name",
    width: 100,
  },
  {
    field: "url",
    headerName: "URL",
    width: 300,
  },
  {
    field: "type",
    headerName: "Event Type",
    width: 100,
  },
  {
    field: "isOpen",
    headerName: "Status",
    description: "This column has a value getter and is not sortable.",
    width: 100,
  },
];

export default function WebhooksDataGrid({ apiRef }) {
  const { session } = React.useContext(sessionContext);
  const navigate = useNavigate();
  React.useEffect(() => {
    if (session.isLoggedIn) {
      session.userData.webhooks.forEach((webhook) => {
        apiRef.current.updateRows([
          {
            id: webhook._id,
            name: webhook.name,
            url:
              window.location.protocol +
              "//" +
              window.location.host +
              "/" +
              webhook.URL,
            type: webhook.EventType,
            isOpen: "open",
          },
        ]);
      });
    }
  }, [session, apiRef]);

  return (
    <Box
      sx={{
        height: 460,
        width: "100%",
        marginTop: "1rem",
        "& .opened": {
          backgroundColor: "rgba(239, 251, 244, 1)",
          color: "#20A144",
          textAlign: "center",
          cursor: "pointer",
        },
        "& .closed": {
          backgroundColor: "#ff943975",
          color: "red",
          cursor: "pointer",
        },
        "& .url": {
          cursor: "pointer",
        },
      }}
    >
      <DataGrid
        rows={rows}
        columns={columns}
        apiRef={apiRef}
        onCellClick={(params) => {
          if (params.field === "isOpen") {
            console.log(params);
            // navigate("/list/" + params.row.type);
            navigate("/list/" + params.row.type, {
              state: { name: params.row.name },
            });
          }
          if (params.field === "url") {
            navigator.clipboard.writeText(params.row.url);
            alert("Link copied to clipboard.");
          }
        }}
        initialState={{
          pagination: {
            paginationModel: { pageSize: 10 },
          },
        }}
        pageSizeOptions={[5, 10, 25]}
        getCellClassName={(params) => {
          if (params.field === "isOpen") {
            return params.value === "open" ? "opened" : "closed";
          }
          if (params.field === "url") {
            return "url";
          }
          return "";
        }}
        checkboxSelection
        disableRowSelectionOnClick
      />
    </Box>
  );
}
